$firstBP: 970px;
$secondBP: 768px;

.collapse {
	&Head {
		background: #ff6060;
		border-radius: 5px;
		height: 50px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 10px;
		color: white;
		font-weight: 500;
		font-size: 24px;
		@media (max-width: 1400px) {
			font-size: 18px;
		}
		@media (max-width: $firstBP) {
			font-size: 13px;

			height: 30px;
		}
		img {
			height: 26px;
			@media (max-width: $firstBP) {
				height: 16px;
			}
		}
	}
	p,
	ul {
		padding: 5px 10px;
		background: #f7f7f7;
		border-radius: 0 0 5px 5px;
		text-align: justify;
		animation: 1s fadeIn;
		animation-fill-mode: forwards;
		color: #ff6060;
		font-weight: 400;
		font-size: 18px;
		visibility: hidden;
		list-style-type: none;
		@media (max-width: $firstBP) {
			font-size: 12px;
		}
	}
}
@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		visibility: visible;
		opacity: 1;
	}
}
