.homeCard {
	display: flex;
	align-items: flex-end;
	height: 300px;
	background: center/cover;
	color: white;
	text-shadow: black 0 0 7px;
	font-size: 18px;
	padding: 20px;
	margin: 25px;
	border-radius: 10px;
	text-decoration: none;
	@media (max-width: 620px) {
		height: 250px;
	}
}
